.navigation {
  background-image: url("./assets/img/hero-img\ \(1\).png");
  width: 100%;
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.profile-pic {
  width: 100%;
  max-width: 250px;
  height: auto;
}

/* Contact form */

label {
  display: inline-block;
  float: left;
  clear: left;
  width: 250px;
  text-align: right;
}
input {
  display: inline-block;
}
/* end of Contact form */

/* Footer */
.social-icon-container {
  margin: auto;
  width: 33.33%;
  column-gap: 5px;
}

.social.icon {
  padding: 20px;
}

/* end of Footer */

@media screen and (max-width: 980px) {
}

@media screen and (max-width: 817px) {
  header div {
    position: relative;
    width: 110%;
  }

  header div h2 {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  h2 {
    font-size: 1.5rem;
  }

  nav ul li span {
    font-size: 0.75em;
  }

  .mobile-header {
    width: 110%;
  }

  .mobile-row {
    display: block;
    margin: 0 100px 0 auto;
  }

  .mobile-view {
    display: inline;
    text-align: end;
  }

  input {
    width: 350px;
  }

  textarea {
    width: 350px;
    height: 120px;
    border: 3px solid #cccccc;
    padding: 5px;
  }

  ol {
    font-size: 0.75rem;
  }
}

